export default {
  data(){
    return {
      showFlag: false
    }
  },
  props: {
    content:{
      type: String,
      default: '内容'
    },
    confirmBtnText: {
      type: String,
      default: '确定'
    },
    cancelBtnText: {
      type: String,
      default: ''
    }
  },
  methods: {
    show(){
      this.showFlag = true;
    },
    hide(){
      this.showFlag = false;
    },
    confirm(){
      this.$emit('confirm');
      this.hide();
    },
    cancel(){
      this.$emit('cancel');
      this.hide();
    }
  }
}



