export default {
    name:'loading',
    data(){
      return {
        showFlag:true
      }
    },
  computed: {
    load(){
      return this.screen ? 'screen' : 'small';
    }
  },
  methods:{
    show(text){
        this.title=text||this.title;
        this.showFlag=true;
    },
    hide(){
        this.showFlag=false;
    }
  },
  props: {
    screen: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '正在载入...'
    }
  }
}



